import { makeAutoObservable } from "mobx"

class storeUser {
    isAuth = false;
    arrBalance = {};

    constructor() {
        makeAutoObservable(this)
    }

    setSession({balance}) {
        this.isAuth = true;

        this.arrBalance = balance;
        //   isLogin, userInfo, need2fa modal  site, need add balance  ..
    }

    clearSession() {
        // isLogin, userInfo = {}, need add balance  ..
        this.isAuth = false;
        this.arrBalance = {};
    }

    countBalance() {
        return this.arrBalance ? Object.keys(this.arrBalance).length : -1;
    }
}

export default new storeUser();